* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1em;
  font-family: Montserrat, "sans-serif";
  background: #FFF;
  color: #000;
}

#root {
  display: flex;
  height: 100vh;
  width: 100vw;
}

#root .user {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20%;
  height: 100%;
  background: #212529;
  color: #FFF;
}

#root .user .user-login form,
#root .user .user-register form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0 0 1em 0;
}

#root .user .user-login form h1,
#root .user .user-register form h1 {
  margin: 0 0 0.5em 0;
}

#root .user .user-login form label,
#root .user .user-register form label {
  margin: 0 0 1em 0;
}

#root .user .user-login form label p,
#root .user .user-register form label p {
  text-align: left;
  font-size: 0.8em;
  margin: 0 0 2px 0;
}

#root .user .user-login form label input,
#root .user .user-register form label input {
  outline: none;
}

#root .user .user-login form input,
#root .user .user-register form input {
  border-radius: 10px;
  border: none;
  padding: 1em;
}

#root .user .user-login form input[type=submit],
#root .user .user-register form input[type=submit] {
  background: #FFF;
  width: 100%;
  transition: 0.2s;
  cursor: pointer;
}

#root .user .user-login form input[type=submit]:hover,
#root .user .user-register form input[type=submit]:hover {
  background: #ebdbbe;
}

#root .user .user-login p,
#root .user .user-register p {
  text-align: center;
  font-size: 0.8em;
}

#root .calendar-event-wrapper {
  display: flex;
  width: 80%;
}

#root .calendar-event-wrapper .event {
  width: 30%;
  color: #000;
  display: flex;
  flex-direction: column;
}

#root .calendar-event-wrapper .event .event-header {
  padding: 2em 0;
  background: #212529;
  color: #FFF;
}

#root .calendar-event-wrapper .event .event-header p {
  text-align: center;
  font-size: 1.4em;
  padding: 0.2em 0;
}

#root .calendar-event-wrapper .event .event-header p:first-child {
  font-weight: 600;
  font-size: 2em;
}

#root .calendar-event-wrapper .event .event-container {
  flex-grow: 2;
}

#root .calendar-event-wrapper .event .event-container .event-item {
  display: flex;
  justify-content: space-between;
  margin: 1em;
  padding: 1em;
  background: #FFF;
  color: #000;
  border: 1px solid #212529;
  border-radius: 10px;
}

#root .calendar-event-wrapper .event .event-container .event-item .event-item-data p {
  text-align: left;
  margin-bottom: 0.5em;
}

#root .calendar-event-wrapper .event .event-container .event-item .event-item-data .event-user {
  font-weight: 600;
}

#root .calendar-event-wrapper .event .event-container .event-item .event-manage-data {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

#root .calendar-event-wrapper .event .event-container .event-item .event-manage-data button {
  background: none;
  border: none;
  cursor: pointer;
}

#root .calendar-event-wrapper .event .event-container .event-item .event-manage-data .event-icon {
  border-radius: 50px;
  width: 1.5em;
  height: 1.5em;
}

#root .calendar-event-wrapper .event .event-container .event-item .event-manage-data .event-icon .event-icon-edit {
  fill: #ffa657;
}

#root .calendar-event-wrapper .event .event-container .event-item .event-manage-data .event-icon .event-icon-delete {
  fill: #f04525;
}

#root .calendar-event-wrapper .event .event-manage {
  width: 100%;
  text-align: center;
}

#root .calendar-event-wrapper .event .event-manage button {
  margin: 0.5em;
  background: #FFF;
  border: 1px solid #212529;
  cursor: pointer;
}

#root .calendar-event-wrapper .event .event-manage .event-icon {
  width: 4em;
  height: 4em;
  border-radius: 50px;
  padding: 1rem;
}

#root .calendar-event-wrapper .event .event-manage .event-icon .event-icon-add {
  fill: #59e76a;
}

#root .calendar-event-wrapper .event .event-manage .event-icon .event-icon-edit {
  fill: #ffa657;
}

#root .calendar-event-wrapper .event .event-manage .event-icon .event-icon-delete {
  fill: #f04525;
}

#root .calendar-event-wrapper .calendar-body {
  display: flex;
  width: 70%;
  height: 100%;
  flex-flow: column;
}

#root .calendar-event-wrapper .calendar-body .calendar-header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: #212529;
  flex: 0 1 auto;
}

#root .calendar-event-wrapper .calendar-body .calendar-header .calendar-month-switch {
  width: 100%;
  padding: 2em 1em;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#root .calendar-event-wrapper .calendar-body .calendar-header .calendar-month-switch .arrow-left .arrow,
#root .calendar-event-wrapper .calendar-body .calendar-header .calendar-month-switch .arrow-right .arrow {
  cursor: pointer;
  width: 3em;
  height: 3em;
  fill: #FFF;
  transition: 0.2s;
}

#root .calendar-event-wrapper .calendar-body .calendar-header .calendar-month-switch .arrow-left .arrow:hover,
#root .calendar-event-wrapper .calendar-body .calendar-header .calendar-month-switch .arrow-right .arrow:hover {
  fill: rgba(125, 136, 146, 0.336);
}

#root .calendar-event-wrapper .calendar-body .calendar-header .calendar-month-switch .calendar-header-date {
  width: 70%;
}

#root .calendar-event-wrapper .calendar-body .calendar-header .calendar-month-switch .calendar-header-date p {
  color: #FFF;
  padding: 0.2em 0;
  text-align: left;
}

#root .calendar-event-wrapper .calendar-body .calendar-header .calendar-month-switch .calendar-header-date p:first-child {
  font-size: 2em;
}

#root .calendar-event-wrapper .calendar-body .calendar-header .calendar-month-switch .calendar-header-date p:nth-child(2) {
  font-size: 1.4em;
}

#root .calendar-event-wrapper .calendar-body .calendar-main {
  background: #FFF;
  flex-grow: 1;
}

#root .calendar-event-wrapper .calendar-body .calendar-main .week-days {
  display: flex;
  color: #000;
  padding: 1em;
  background: #ebdbbe;
}

#root .calendar-event-wrapper .calendar-body .calendar-main .week-days .week-day {
  text-align: center;
  font-weight: 600;
  width: calc(100% / 7);
}

#root .calendar-event-wrapper .calendar-body .calendar-main .calendar-days {
  display: flex;
  flex-wrap: wrap;
  color: #000;
}

#root .calendar-event-wrapper .calendar-body .calendar-main .calendar-days .calendar-day {
  min-height: 5em;
  width: calc(100% / 7);
}

#root .calendar-event-wrapper .calendar-body .calendar-main .calendar-days .current-day {
  font-weight: 600;
}

#root .calendar-event-wrapper .calendar-body .calendar-main .calendar-days .full {
  display: flex;
  flex-direction: column;
  cursor: pointer;
  transition: 0.2s;
}

#root .calendar-event-wrapper .calendar-body .calendar-main .calendar-days .full:nth-child(even) {
  background: #EEE;
}

#root .calendar-event-wrapper .calendar-body .calendar-main .calendar-days .full:nth-child(even):hover {
  background: #d3d7da;
}

#root .calendar-event-wrapper .calendar-body .calendar-main .calendar-days .full:nth-child(7n) {
  background: #ebdbbe;
}

#root .calendar-event-wrapper .calendar-body .calendar-main .calendar-days .full:nth-child(7n):hover {
  background: #d8c192;
}

#root .calendar-event-wrapper .calendar-body .calendar-main .calendar-days .full:hover {
  background: #EEE;
}

#root .calendar-event-wrapper .calendar-body .calendar-main .calendar-days .full .selected {
  font-weight: 600;
}

#root .calendar-event-wrapper .calendar-body .calendar-main .calendar-days .full .calendar-day-number {
  font-size: 1.3em;
  margin: 0.5em 0 0.5em 0.5em;
}

#root .calendar-event-wrapper .calendar-body .calendar-main .calendar-days .full .calendar-day-event-date {
  margin: 0 0 0.5em 0.5em;
}

#root .calendar-event-wrapper .calendar-body .calendar-main .calendar-days .empty {
  visibility: hidden;
}

